@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}body{font-family:"Lato"}.info{height:450px;overflow:hidden}.ipBox{display:flex;align-items:center}.ipBox .subtitle{color:#8c8c8c}.title-bar{width:100%;height:40px;background-color:#0676ed;display:flex;color:#ffffff !important;padding:5px 5px;-webkit-app-region:drag}.actions{-webkit-app-region:no-drag;margin-left:auto;padding-top:5px}.App{margin:10px 80px}.result{display:flex;justify-content:space-between}::-webkit-scrollbar{width:2px;height:2px}::-webkit-scrollbar-button{width:0px;height:0px}::-webkit-scrollbar-thumb{background:#737373;border:0px none #ffffff;border-radius:86px}::-webkit-scrollbar-thumb:hover{background:#ffffff}::-webkit-scrollbar-thumb:active{background:#000000}::-webkit-scrollbar-track{background:#ffffff;border:0px none #ffffff;border-radius:29px}::-webkit-scrollbar-track:hover{background:#666666}::-webkit-scrollbar-track:active{background:#333333}::-webkit-scrollbar-corner{background:transparent}

.traffic-lights{top:1px;left:8px}.focus .traffic-lights>.traffic-light-close,.traffic-lights:hover>.traffic-light-close,.traffic-lights:active>.traffic-light-close{background-color:#ff6159}.focus .traffic-lights>.traffic-light-close:active:hover,.traffic-lights:hover>.traffic-light-close:active:hover,.traffic-lights:active>.traffic-light-close:active:hover{background-color:#bf4942}.focus .traffic-lights>.traffic-light-minimize,.traffic-lights:hover>.traffic-light-minimize,.traffic-lights:active>.traffic-light-minimize{background-color:#ffbd2e}.focus .traffic-lights>.traffic-light-minimize:active:hover,.traffic-lights:hover>.traffic-light-minimize:active:hover,.traffic-lights:active>.traffic-light-minimize:active:hover{background-color:#bf8e22}.focus .traffic-lights>.traffic-light-maximize,.traffic-lights:hover>.traffic-light-maximize,.traffic-lights:active>.traffic-light-maximize{background-color:#28c941}.focus .traffic-lights>.traffic-light-maximize:active:hover,.traffic-lights:hover>.traffic-light-maximize:active:hover,.traffic-lights:active>.traffic-light-maximize:active:hover{background-color:#1d9730}.traffic-lights>.traffic-light:before,.traffic-lights>.traffic-light:after{visibility:hidden}.traffic-lights:hover>.traffic-light:before,.traffic-lights:hover>.traffic-light:after,.traffic-lights:active>.traffic-light:before,.traffic-lights:active>.traffic-light:after{visibility:visible}.traffic-light{border-radius:100%;padding:0;height:12px;width:12px;border:1px solid rgba(0,0,0,0.06);box-sizing:border-box;margin-right:3.5px;background-color:#ddd;position:relative;outline:none}.traffic-light:before,.traffic-light:after{content:"";position:absolute;border-radius:1px;left:0;top:0;right:0;bottom:0;margin:auto}.traffic-light-close:before,.traffic-light-close:after{background-color:#4d0000;width:8px;height:1px}.traffic-light-close:before{transform:rotate(45deg)}.traffic-light-close:after{transform:rotate(-45deg)}.traffic-light-close:active:hover:before,.traffic-light-close:active:hover:after{background-color:#190000}.traffic-light-minimize:before{background-color:#995700;width:8px;height:1px}.traffic-light-minimize:active:hover:before{background-color:#592800}.traffic-light-maximize:before{background-color:#006500;width:6px;height:6px}.traffic-light-maximize:after{background-color:#28c941;width:10px;height:2px;transform:rotate(45deg)}.traffic-light-maximize:active:hover:before{background-color:#003200}.traffic-light-maximize:active:hover:after{background-color:#1d9730}body{font-family:"Helvetica Neue", Helvetica, Arial, sans-serif;font-weight:100}h1,h2{font-weight:100}h2{margin:0 0 10px}.example{margin:0 0 30px}.container{width:300px;margin:0 auto}

